import React from 'react';

import TemplatePreview from './TemplateList/TemplatePreview';
import Loader from './Loader';

export default class TemplateList extends React.Component {

    onSelectTemplate = () => {
        return this.props.onSelectTemplate ? this.props.onSelectTemplate() : null;
    }

    render() {
        if (!this.props.templates) return <Loader />;
        return (
            <ul className="template-list clean-list">
                {this.props.templates.map(template =>
                    <TemplatePreview key={template._id} template={template} onSelectTemplate={this.onSelectTemplate} />)}
            </ul>
        )
    }
}
